
#tooltip {
  white-space: pre-wrap;
  position: absolute;
  z-index: 10;
  visibility: hidden;
  background: white;
  border: 1px solid black;
  border-radius: .25em;
  padding: .25em;
  text-align: center;
}

#graph {
  display: inline-block;
}

#title {
  font-size: 1.25em;
}



.cell:hover {
  stroke: black;
  stroke-width: 1;
}



.legend-cell {
  stroke: black;
  stroke-width: 1;
}